@import url(https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Roboto Slab";
  src: local("Reboto Slab"),
   url(/static/media/RobotoSlab-VariableFont_wght.7542a013.ttf) format("truetype");
  font-weight: regular;
 }

@font-face {
  font-family: "Ubuntu Mono";
  src: local("Ubuntu Mono"),
   url(/static/media/UbuntuMono-Regular.c8ca9c5c.ttf) format("truetype");
  /* font-weight: regular; */
 }

 


.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
  
    }
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .Tabs {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .Tabs button {
    margin: 0 10px;
    padding: 10px 20px;
    border: none;
    background-color: #ccc;
    cursor: pointer;
  }
  
  .Tabs button:hover {
    background-color: #ddd;
  }
  
  .TabContent {
    padding: 20px;
  }
  
  .main {
    display: flex;
    height: 100vh;
    width: 100%;
    align-items: center;
    flex-direction: column;
    grid-row-gap: 20px;
    row-gap: 20px;
  }
  
  h1 {
    margin: 10px;
    font-size: 40px;
    color: rgb(1, 1, 59);
  }
  
  .search {
    width: 30%;
  }
  
  /* ul li {
    font-size: 20px;
  }  */
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: "Ubuntu Mono", "Roboto Slab", "Press Start 2P";;
  }
  
  header {
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.07), 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    color: #212529;
  }
  
  .nav-area {
    color:#1d1600de;
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px 20px;
  }
  
  .logo {
    text-decoration: none;
    /* font-size: 25px; */
    color: inherit;
    margin-right: 20px;
  }
  
  .menus {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    list-style: none;
  }
  
  .menu-items {
    position: relative;
    font-size: 20px;
    font-family: "Ubuntu Mono", "Roboto Slab", "Press Start 2P";
  }
  
  .menu-items a {
    display: block;
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    text-decoration: none;
  }
  
  .menu-items button {
    display: flex;
    align-items: center;
    color: inherit;
    font-size: inherit;
    font-family: inherit;
    border: none;
    background-color: transparent;
    cursor: pointer;
    width: 100%;
  }
  
  button span {
    margin-left: 3px;
  }
  
  .menu-items > a, .menu-items button {
    text-align: left;
    padding: 0.7rem 1rem;
  }
  
  .menu-items a:hover,
  .menu-items button:hover {
    background-color: #f2f2f2;
  }
  
  .arrow::after {
    content: "";
    display: inline-block;
    margin-left: 0.28em;
    vertical-align: 0.09em;
    border-top: 0.42em solid;
    border-right: 0.32em solid transparent;
    border-left: 0.32em solid transparent;
  }
  
  .dropdown {
    position: absolute;
    right: 0;
    left: auto;
    box-shadow: 0 10px 15px -3px rgba(46, 41, 51, 0.08),
      0 4px 6px -2px rgba(71, 63, 79, 0.16);
    font-size: 0.875rem;
    z-index: 9999;
    min-width: 10rem;
    padding: 0.5rem 0;
    list-style: none;
    background-color: #fff;
    border-radius: 0.5rem;
    display: none;
  }
  
  .dropdown.show {
    display: block;
  }
  
  .dropdown .dropdown-submenu {
    position: absolute;
    left: 100%;
    top: -7px;
  }
  
  /* content */
  
  .content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 3rem 20px;
  }
  
  .content h2 {
    margin-bottom: 1rem;
  }
  
  .content a {
    color: #cc3852;
    margin-right: 10px;
  }
  
  .about{
    /* margin-left: 10; */
    font-weight:600;
    font-family: "Ubuntu Mono", "Roboto Slab", "Press Start 2P";
    font-size:calc(7px + 2vmin);
  }
  .hero{
    
    /* margin-top: 0; */
    /* margin-left:0; */
    size: 10cm;
    background-color: #4870dd;
    /* background-image:; */
    /* background-image: url('../assets/background.png'); */
    /* background-size: contain; */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: row;
    color: '#0fffff';
    text-align: right;
    padding: 100px 100px;
    /* font-family: "Press Start 2P"; */
    font-family: "Ubuntu Mono", "Roboto Slab", "Press Start 2P";
    font-weight:900;
  }
  .Achievement-name{
    color:#212529;
    text-align: left;
    font-weight:700;
    font-family: "Ubuntu Mono", "Roboto Slab", "Press Start 2P";
  }
  .Achievement-desc{
    color:cadetblue;
    text-align: left;
    font-weight: 300;
    font-family: "Ubuntu Mono", "Roboto Slab", "Press Start 2P";
  }
  
